export default {
	data() {
		return {
			name : "",
			email: "",
			password: ""
		}
	},
	methods: {
		loginSubmit() {
			this.toCheck(this.email, this.password,this.name)
			if (this.toCheck(this.email, this.password,this.name)) {
				// 将要发起请求 
				uni.request({
					url: 'http://47.96.156.131:5000/keep/users/register',
					method: 'POST',
					header: {},
					data: {
						name : this.name,
						email: this.email,
						password: this.password
					},
					success: (res) => {
						if (res.data.callbackEmail) {
							uni.showToast({
								icon: 'none',
								title: res.data.email
							})
						}
						if (res.data._id) {
							uni.navigateTo({
								url: './login'
							})
						}
						
					}
				})
			}
		},
		loginToRegister() {
			uni.navigateTo({
				url: './register'
			})
		},
		toCheck(username, password,name) {
			if (!this.$validate.isEmail(username)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的邮箱格式'
				})
				return 0
			} else if (!this.$validate.isPwd(password)) {
				uni.showToast({
					icon: 'none',
					title: '请输入6为的密码'
				})
				return 0
			}
			else if (!this.$validate.isName(name)) {
				uni.showToast({
					icon: 'none',
					title: '正确格式的用户名3-6字符'
				})
				return 0
			}
			return 1
		}
	},
	onLoad() {}
}
